import ApiClientBase from './ApiClientBase';

export default class SessionClient extends ApiClientBase
{
    analyzeImpact(data, targetColumn, startDate, endDate, resultInterval, isEstimate, transformFunc, eventName='', statusCallbackUrl=''){
        
        var parameters = {
            targetColumn : targetColumn, 
            startDate: startDate, 
            endDate: endDate, 
            isEstimate: isEstimate, 
            resultInterval: resultInterval
        };

        if(eventName.length > 0){
             Object.defineProperty(parameters, 'eventName', {
                value: eventName,
                enumerable: true
            });
        }

        if(statusCallbackUrl.length > 0){
            Object.defineProperty(parameters, 'callbackUrl', {
                value: statusCallbackUrl,
                enumerable: true
            });
        }

        return this._apiConnection.post('sessions/impact', data, transformFunc, parameters);
    }

    createForecast(dataSetName, targetColumn, startDate, endDate, resultInterval, statusCallbackUrl, transformFunc){
        var parameters = {
            dataSetName: dataSetName,
            targetColumn : targetColumn, 
            startDate: startDate, 
            endDate: endDate,
            resultInterval: resultInterval
        };

        if(statusCallbackUrl.length > 0){
            Object.defineProperty(parameters, 'callbackUrl', {
                value: statusCallbackUrl,
                enumerable: true
            });
        }

        return this._apiConnection.post('sessions/forecast', {}, transformFunc, parameters);
    }

    estimateForecast(dataSetName, targetColumn, startDate, endDate, resultInterval, statusCallbackUrl, transformFunc){
        var parameters = {
            dataSetName: dataSetName,
            targetColumn : targetColumn, 
            startDate: startDate, 
            endDate: endDate,
            resultInterval: resultInterval
        };

        if(statusCallbackUrl.length > 0){
            Object.defineProperty(parameters, 'callbackUrl', {
                value: statusCallbackUrl,
                enumerable: true
            });
        }

        return this._apiConnection.post('sessions/forecast', {}, transformFunc, parameters);
    }

    estimateImpact(data, eventName, targetColumn, startDate, endDate, resultInterval, statusCallbackUrl, transformFunc){
       var parameters = {
            eventName: eventName,
            targetColumn : targetColumn, 
            startDate: startDate, 
            endDate: endDate,
            resultInterval: resultInterval
        };

        if(statusCallbackUrl.length > 0){
            Object.defineProperty(parameters, 'callbackUrl', {
                value: statusCallbackUrl,
                enumerable: true
            });
        }

        return this._apiConnection.post('sessions/impact', data, transformFunc, parameters);
    }

    get(id, transformFunc){
        return this._apiConnection.get(`sessions/${id}`,transformFunc);
    }

    getStatus(id, transformFunc){
        return this._apiConnection.head(`sessions/${id}`,transformFunc);
    }

    remove(id, transformFunc){
        return this._apiConnection.delete(`sessions/${id}`,transformFunc);
    }

    getResults(id, transformFunc){
        return this._apiConnection.get(`sessions/${id}/results`,transformFunc);
    }

    list(dataSetName, eventName, requestedAfterDate, requestedBeforeDate, transformFunc){
        var parameters = {
            dataSetName: dataSetName,
            eventName : eventName, 
            requestedAfterDate: requestedAfterDate, 
            requestedBeforeDate: requestedBeforeDate
        };

        this._apiConnection.get('sessions', transformFunc, parameters);
    }
}


